    title: 'Siber Fiziksel Teknolojilerle Tarımda Su Tasarrufu Sağlamak Mümkün - Haber Gündemim',
    src: '/static/images/news/4.jpg',
    content: `<p>Suya yönelik ihtiyacın artışın en önemli nedenlerinden birisi bulunduğumuz coğrafyada küresel ısınmanın ve toprakların çoraklaşmasının olumsuz etkileri olarak görülüyor. SmartMole's kurucusu Ziraat Yüksek Mühendisi A.Ömer Yuluğ’a göre, 2025-2040 yılları arasında nüfusun artış hızı, göç, küresel ısınma, yanında, yer altı suların özellikle aşırı kimyasal, gübre ve sulamayla yaşadığı kirlilik yüzünden, su fakirliği çekilebilir. Doğa kanunlarıyla uyumlu siber fiziksel teknolojilerle tarımda su tasarrufu sağlamak mümkün. Siber fiziksel teknolojileri ile tarımda ve peyzajda çiftçinin üretim gözü olarak %50’den fazla su ve gübre tasarrufu sağlayan SmartMole's kurucusu Ziraat Yüksek Mühendisi A.Ömer Yuluğ, DSİ verilerine göre, Türkiye’nin yıllık 94 Milyar m3 kullanılabilir yüzey suyu olduğunu ve yıllık çekilebilen yer altı su kaynağın 18 milyar m3 olduğunu belirtti. Toplamda 112 milyar m3 kullanılabilir su potansiyelin olduğu. A.Ömer Yuluğ, Türkiye’de su sıkıntısına ve su kullanım alanlarında nasıl tasarruf edilebileceğini söyledi. 112 milyar m3 suyun 44 milyar m3’ü sulama amaçlı kullanılmakta.
    </p>
    <br/>
    <p>
    Türkiye’de kişi başına düşen kullanılabilir yıllık su miktarı 2000 yılında 1652 m3, 2009 yılında 1544 m3, 2020 yılında 1346 m3 oldu. Kişi başına yıllık su tüketimi 1000 m3’ten az olan ülkeler su fakiri olarak kabul ediliyor. 2025-2040 yılları arasında nüfusun artış hızı, göç, küresel ısınma, temiz su kaynaklarının tükeniş hızı yanında, yer altı sularının özellikle aşırı kimyasal, gübre ve sulamayla yaşadığı kirlilik yüzünden, su fakirliği çekilebileceğini ve gıda yeterliliğinin de risk altında olacağı sinyalleri önemsenmeli. Suyun %73’ünün tarımda kullanılması sürdürülmemeli. Damla sulama yönteminin yüzey sulama randımanında çalıştırılma probleminin tüm dünyada devam ettiğini; “Sulama yöntemini değiştirmek yeterli değildir. Bu fiziki yapının canlının su ihtiyaçlarına, mikroklimaya, iklime, vejetatif döneme, toprak ve bitki sağlığına, suyun ve enerjinin verimli kullanımına vb. hizmet edilebilmesi için mutlaka doğa ile uyumlu yönetim teknolojilerine ihtiyacı vardır.”
    </p>
    <br/>
    <p>
    Akıllı sulama teknolojileriyle, bitkinin etkili kök bölgesi altına suyun, gübrenin kaçmasının engellemesi ve gerçek zamanlı olarak sulama sistemlerinin yönetilmesinin mümkün olduğunu; “İnsansal hatalardan kurtulmalıyız. Bunun yolu konunun uzmanı insanlardan feyz alabilen doğa kanunlarıyla uyumlu bilinmeyene dair paradigmalar üretebilen yapay zeka destekli siber fiziksel Tarım 5.0 teknolojilerinin yaygınlaşmasıdır. Bitkilerini gelişim periodlarında oluşan farklı su ihtiyaçlarını çiftçi koşullarında doğru tespit edip karşılarken o sulama yönteminden bilimsel olarak beklenen tasarrufun fazlasını verim kaybına sebebiyet vermeden yaptırabiliyor. Bilimsel hedefler ile çiftçinin gerçekleştirmekte olduğu su, gübre, işçilik, kimyasal kullanımı arasındaki doğayı kirleten ve çiftçiye zarar ettiren kısmını SmartMole’s yok ediyor. İnsansal hataları elimine ederek doğa kanunları çerçevesinde olması gerekeni yapmakta çiftçinin göremediklerini görmelerini ve eğitilmelerini sağlayarak başarılı olabiliyoruz. Toprakta bulunan bio-çeşitliliğin korunmasına teknolojimiz destek veriyor. Etkili kök bölgesinin dünyada ilk defa tanımlayabilmemiz ile bu bölgedeki bakteri ve mikroorganizmaların korunması ve arttırılmasında onarıcı tarım enstrümanlarının kullanımını destekliyoruz. Yerli ve milli SmartMole’s tarım 5.0 sensör ve su yönetimi teknoloji ekolü dünyada bir ilktir. Doğa kanunları ile %100 uyumludur. Bu ürünü geliştirdik, onu talep ederek doğaya, işletmelerine fayda sağlamak ise çiftçilere ve yerel idarelere düşen görevdir” şeklinde görüşlerini belirtti.
    </p>
    `,
